import uniCalendar from '@/components/uni-calendar/uni-calendar.vue';
import taskBackgroundImage from "@/static/img/task_bg.png"
import taskHistoryBackgroundImage from "@/static/img/history_task_bg.png"
function nowDate(){
	let nowDate = new Date();
	return nowDate.getFullYear() + '-' + ('0'+(nowDate.getMonth() + 1)).slice(-2) + '-' + ('0'+nowDate.getDate()).slice(-2);
}
export default {
	components:{
		uniCalendar
	},
    data() {
        return {
            email: '',
			taskBackgroundImage:taskBackgroundImage,
			taskHistoryBackgroundImage:taskHistoryBackgroundImage,
			historyTaskList:[],
			beingTaskList:[],
			date:nowDate(),
			userType:getApp().globalData.userType
        }
    },
    methods: {
		open() {
			this.$refs.calendar.open();
		},
		dateClose(){
			
		},
		dateConfirm(e){
			this.date = e.fulldate;
		}	
    },
	onLoad() {
		// uni.loadFontFace({
		//     family: 'ikingFont',
		//     // 本地字体路径需转换为平台绝对路径
		//


		//


		// 	success() {
		//         console.log('success')
		//     },
		//     fail(e) {
		//         console.log('fail',JSON.stringify(e))
		//     }
		// })
		// console.log(this.$refs)
		this.historyTaskList = getApp().globalData.dataJson.list.map(function(e){
			switch (e.status){
				case 'waitApprove':
					e.statusName = '待审批'
				break;
				case 'waitStart':
					e.statusName = '审批通过'
				break;
				case 'noApprove':
					e.statusName = '拒绝审批'
				break;
				case 'being':
					e.statusName = '执行中'
				break;
				case 'waitEval':
					e.statusName = '待评价'
				break;
				case 'finish':
					e.statusName = '已结束'
				break;
			}
			return e
		})
		this.beingTaskList = this.historyTaskList.filter(function(e){
			return e.status != 'finish';
		})
		this.historyTaskList= this.historyTaskList.filter(function(e){
			return e.status == 'finish';
		})
	}
}

